:root {
  background-color: black;
}

.movie-cast-main {
  display: flex;
  padding-top: 0vh;
  padding-bottom: 1vh;
  background-color: transparent;
  height: 50vh;
  width: 90%;
  color: purple;
  overflow-y: hidden;
  overflow-x: scroll;
  margin: auto;
  -ms-scroll-snap-type: x mandatory;
      scroll-snap-type: x mandatory;
}

.movie-cast-title {
  width: 90%;
  margin: auto;
  font-size: xxx-large;
  padding-top: 5vh;
  color: #ff8906;
  font-weight: 700;
}

.card-container {
  display: flex;
  flex-direction: column;
  margin-right: 10px;
  border-radius: 8px;
  width: 150px;
  max-width: 150px;
