:root{
  background-color: black;
}

.similar-movies-title{
  width: 90%;
  margin: auto;
  padding-top: 5vh;
  font-size: xx-large;
  color:#ff8906;
}

.favorites-title{
  width: 90%;
  margin: auto;
 
  font-size: xx-large;
  color:#ff8906;
}
.favorites-subtitle{
  width: 90%;
  margin: auto;
  color:#ff8906;
}

.similar-movies-section{
  overflow-x: scroll;
  width: 90%;
  margin: auto;
  display: flex;
  color: black;
  height: 60vh;
  padding-top: 10vh;
  margin-bottom: 10vh;
    
}

.similar-movies-container{
 
  color: wheat;
  margin: 5px;
  cursor: pointer;
  padding-top: 10px;
  background-color: transparent;
  height: 30px;
  padding-right: 10px;
  padding-left: 10px;
}

.similar-movies-img{
  border-radius: 9px;
  position:static;
  width: 150px;
  height: 225px;
transition: 400ms;
  

}
.similar-movies-img:hover{
 
  padding-bottom: 2vh;
  transform: scale(1.1);
  opacity: 0.8;
}

.photo-loading{
  color: black;
}

.empty-favorites{
  flex-direction: column;
  width: 90%;
  display: flex;
  justify-content: center;
  align-items: center;
 margin: auto;
 background-color:rgba(245, 222, 179, 0.096);
 height: 40vh;
 margin-bottom: 10vh;
}
.empty-favorites-text {
  color: wheat;
  text-align: center;
}

.addBoxIcon{
  color:#ff8906;
  transform: scale(2.0);
  margin-bottom: 30px;
}