@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap');



*{
    margin: 0;
    padding: 0;
    font-family: 'Bebas Neue', 'arial'
}

flexbox {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

//-------------------------------------HeaderSection----------------------------------
.nav-header{
    display: flex;
    justify-content: space-between;
    margin-right: 100px;
    
   position: fixed;
    height: 10vh;
    text-align: center;
    width: 100%;
    
    gap: 0px 0px; 
    margin: auto;
    align-items: center;
    background-color: transparent;
    opacity: 0.8; // default 0.8
    z-index:1;
    color: white;
    text-decoration: none;
    transition: 50ms;
   
}

.nav-header-below{
    @extend .nav-header;
    position: fixed;
    background-color: black;
    opacity: 0.8;
    -webkit-box-shadow: 0 7px 6px rgba(57, 63, 72, 0.61);
    -moz-box-shadow: 0 7px 6px rgba(57, 63, 72, 0.836);
    box-shadow: 0 7px 6px rgba(57, 63, 72, 0.849);
    cursor:default;
  
    
}




.nav-list{
    cursor:default;
    background-color: transparent;
    color: white;
    text-decoration: none;
    display: flex;
    justify-content: space-around;
    align-items: center;

    a{
        background-color: transparent;
        color: white;
        text-decoration: none;
        cursor:default;
        
    }
    :hover{
        color: #90cea1;
        cursor:pointer;
    }
}

.header-logo{
    cursor: pointer;
    background-color: transparent;
    width: 200px;
    padding-left:  20px;
}
.header-logo-square{
    cursor: pointer;
    background-color: transparent;
    width: 100px;
    padding-left:  20px;
}
//..................................................................SEARCHBAR SECTION...


.search-icon-container{
    display: flex;
    align-items: center;
    justify-content: center;
    background-color:transparent;
    width: 15%;
    height: 100%;
}
.search-button{
    color: #90cea1;
    transform: scale(1.5);
    cursor: pointer;
    background-color:transparent;
}
.search-button-close{
    @extend .search-button;
    color: red;

}

.search-container{
    display: grid;
    grid-template-columns: 90% 10%;
   
    width: 100%;
    z-index: 1;
    position: fixed;
    height: 5vh;
    margin-top: 10vh;
    backdrop-filter: blur(8px);
    animation: searchanim 300ms ;

   .search-input{
    z-index: -1;
    padding-left: 3%;
    outline: none;
    border: none;
    background-color:white;

    }

    .search-clear-button{
        color: #ccc;
        width: 100%;
        height: 100%;
        background-color: #ffffff;
        margin: auto;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        border: none;
    }
    .search-clear-button-red{
        @extend .search-clear-button;
        color: #e53170;
    }
}
@keyframes searchanim {
    0%{ opacity: 0;}
    100%{opacity: 1;}
}

.default-search{
    padding-left: 2%;
    margin-top: 16vh;
    color: #ff8906;
    position: fixed;
    z-index: 2;
}

.results-container{
    height: auto;
    display: flex;
   padding-top: 5vh;
   width: 100%;
   backdrop-filter: blur(4px);
    z-index: 1;
    margin-top: 15vh;
    position: fixed;
   color: wheat;
   overflow-x:scroll;
   overflow-y: hidden;
    -webkit-box-shadow: 0 7px 6px rgba(57, 63, 72, 0.3);
    -moz-box-shadow: 0 7px 6px rgba(57, 63, 72, 0.3);
    box-shadow: 0 7px 6px rgba(57, 63, 72, 0.3);
    scroll-snap-type: x mandatory;
}

.no-results-container{
    font-size: x-large;
   display: flex;
   align-items: center;
    padding-left: 3%;
    color: white;
    padding-block: 1vh;
    margin-top: 15vh;
    z-index: 1;
    position: fixed;
    width: 100%;
    height: 6vh;
   backdrop-filter: blur(8px);
    -webkit-box-shadow: 0 7px 6px rgba(57, 63, 72, 0.3);
    -moz-box-shadow: 0 7px 6px rgba(57, 63, 72, 0.3);
    box-shadow: 0 7px 6px rgba(57, 63, 72, 0.3);
    .no-results-movie{
        color: #ff8906;
    }
}


.search-result{
    scroll-snap-align: start;
    padding-left: 1.5%;
    padding-right: 1.5%;
    padding-top: 10px;
    background-color: transparent;
    width: 100px;
    height: 240px;
  
   
}

.search-item-img{
    border-radius: 5px;
    cursor: pointer;
    width: 100px;
    height: 150px;
}













//---------------------------------------------------------------------MAIN SECTION 
.loader-container{
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: black;
    width: 100%;
    height: 110vh;
}
.loader {
    border: 0 solid transparent;
    border-radius: 50%;
    width: 100px;
    height: 100px;
   }
   
   .loader::before,
   .loader::after {
    content: '';
    border: 7px solid #ccc;
    border-radius: 50%;
    width: inherit;
    height: inherit;
    position: absolute;
    animation: loader 2s linear infinite;
    opacity: 0;
   }
   
   .loader::before {
    animation-delay: 1s;
   }
   
   @keyframes loader {
    0% {
     transform: scale(1);
     opacity: 0;
    }
   
    50% {
     opacity: 1;
    }
   
    100% {
     transform: scale(0);
     opacity: 0;
    }
   }



.info-container{
    color: #39bda7;
    opacity: 1;
    max-height: 100vh;
    margin: auto;
    background-size:cover;
    background-repeat: no-repeat;
