//---------------------------------------------------------------mainBUTTONS

.button-container{
    margin-top: 10px;
    text-shadow: 0.5px 0.5px rgb(12, 7, 1) ;
    display: flex;
    justify-content: space-between;
    background-color:transparent;
    padding-block: 1%;
    width: 250px;
    cursor: pointer;
    height: 6vh;
    

    .main-button{
        text-shadow: 0.5px 0.5px rgb(49, 49, 49) ;
        cursor: pointer;
        display: flex;
        justify-content: space-evenly;
        padding-right: 0px;
        align-items: center;
        background-color: transparent;
        width: 120px;
        border: 1px solid #ffffff;
        border-radius: 4px;
        transition: all 0.2s ease-in;
        position: relative;
        overflow: hidden;
        font-size: 17px;
        color: rgb(255, 255, 255);
        z-index: 0;

        .button-icon{
            font-size: x-large;
            color:white;
        }
    }

    .main-button:before {
        text-shadow: 0.5px 0.5px rgb(49, 49, 49) ;
        content: "";
        position: absolute;
        left: 50%;
        transform: translateX(-50%) scaleY(1) scaleX(1.25);
        top: 100%;
        width: 140%;
        height: 180%;
        background-color: rgba(223, 10, 10, 0.05);
        border-radius: 50%;
        display: block;
        transition: all 0.5s 0.1s cubic-bezier(0.55, 0, 0.1, 1);
        z-index: -1;
       }
       .main-button:after {
        text-shadow: 0.5px 0.5px rgb(49, 49, 49) ;
        content: "";
        position: absolute;
        left: 55%;
        transform: translateX(-50%) scaleY(1) scaleX(1.45);
        top: 180%;
        width: 160%;
        height: 190%;
        background-color:#01b4e4;
        border-radius: 50%;
        display: block;
        transition: all 0.5s 0.1s cubic-bezier(0.55, 0, 0.1, 1);
        z-index: -1;
       }

       .main-button:hover {
        text-shadow: 0.5px 0.5px rgb(49, 49, 49) ;
        color: #ffffff;
        border: 1px solid #fdfdfd;
       }
       
       .main-button:hover:before {
        text-shadow: 0.5px 0.5px rgb(49, 49, 49) ;
        top: -35%;
        background-color:#ff8906;
        transform: translateX(-50%) scaleY(1.3) scaleX(0.8);
       }
       
       .main-button:hover:after {
        text-shadow: 0.5px 0.5px rgb(49, 49, 49) ;
        top: -45%;
        background-color:#ff8906;
        transform: translateX(-50%) scaleY(1.3) scaleX(0.8);
       }
    
}



