:root{
    background-color: black;
}

.movie-cast-main{
    
    display: flex;
    padding-top: 0vh;
    padding-bottom: 1vh;
    background-color:transparent;
    height: 50vh;
    width: 90%;
    color: purple;
    overflow-y: hidden;
    overflow-x: scroll;
    margin: auto;
    scroll-snap-type: x mandatory;
}

.movie-cast-title{
    width: 90%;
    margin: auto;
    font-size: xxx-large;
    padding-top: 5vh;
    color:#ff8906;
    font-weight: 700;
}


// ----------------------------------------------------------------- CARD CARD CARD CARD--------------//

.card-container{
    display: flex;
    flex-direction: column;
    margin-right: 10px;
    border-radius: 8px;
    width: 150px;
    max-width: 150px;
    scroll-snap-align: start;
}


.card-photo{
    padding: 10px;
    border-radius: 21px;
    width: 120px;
    height: 180px;
    align-items: center;
    max-width: 150px;

   
}
.card-name{
    color: wheat;
    padding-left: 10px;
    width: 120px;
    max-width: 120px;
}
.card-character{
    width: 120px;
    max-width: 120px;
    color: wheat;
    padding-left: 10px;
}