:root{
    background-color: black;
}

.trending-title{
    
    width: 90%;
    max-width: 90%;
    color: #ff8906;
    margin: auto;
}

.similarmovies-title{
    @extend .trending-title;
    padding-top: 10vh;
}


.trending-slider{
    margin-top: 10px;
    padding-top: 10px;
    height: 50vh;
    margin: auto;
    max-width: 100%;
    width: 90%;
    background-color: transparent;
   
    color:wheat;
}
.slider-container{
    padding-top: 5vh;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: auto;
    flex-direction: column;
    padding-left: 10px;
    background-color:transparent;
    width: 160px;
    max-width: 160px;
    height: 280px;
    max-height: 280px;

    
}
.slider-container, .p{
    
    font-size: medium  ;
}
.trending-slider-img{
    cursor: pointer;
    border-radius: 10px;
    width: 150px;
    height: 225px;
    transition: 300ms;
    
}
.trending-slider-img:hover{
    margin-bottom: 12px;
    transform: scale(1.1);
    opacity: 0.8;
  
}