// large devices (tablets, 992px and up)
@media  screen and (max-width: 992px) {
    .main-genres{
        display: flex;
        flex-flow: column;
    }
    .genres-info-one{
        display: flex;
    }
    .genres-info-two{
        display: flex;
    }
  
    .trending-title{
        top: -100px;
    }
    .trending-slider{
        height: 30vh;
    }
    .info-container{
        height: 90vh;
    }
} 

// Medium devices (tablets, 768px and up)
@media  screen and (max-width: 769px) {


    .detail-image-container{
       height: 500px;
        
    }
    .detail-info-container{
        max-height: 550px;
    }
    .detail-information{
        transform: scale(0.9);
    
    }
    .crew-container{
        display: block;
    }
    
    
    .movie-cast-main{
        height: 260px;
    }
    .similarmovies-title{
        padding-top: 1vh;
    }
    .trending-slider{
        height: 320px;
    }
    .play-trailer-button{
        transform: scale(0.9);
    }
    .play-trailer-button{
        margin-left: -20px;
    }
    .trending-title{
        padding-top: 5vh;
    }
    .main-info{
        left: 0px;
        transform: scale(0.9);
    }
    .main-info .main-overview {
        background-color: transparent;
        height: auto;
        font-size: small;
        width: 120%;
    }
} 

@media  screen and (max-width: 676px) {
   
    .play-trailer-button{
        margin-left: none;
        transform: scale(0.8);
    }

    .detail-info-container{
        width: 95%;
    }
    .detail-information{
        padding: 0;
        margin: 0;
    }

    .details-poster{
       width: 300px;
       height: 450px;
    }
    .play-trailer-button{
        margin-left: -20px;
    }
    .tagline-overview-container{
        padding-top: 15vh;
        width: 95%;
    }
}




// Small devices (landscape phones, 576px and up)
@media  screen and (max-width: 576px) {
    .trending-slider{
        width: 80%;
    }
    .details-poster{
        max-width: 200px;
        max-height: 300px;
    }

    .title-year{
        padding-top: 0px;
    }
    .details-poster{
       margin-top: 5vh;
    }
    .play-trailer-button{
        padding-top: 6vh;
    margin-left: -255px;
    }

    .detail-info-container{
      height: fit-content;
    }

    .tagline-overview-container{
        padding-top: 12vh;
        width: 95%;
    }
    .movie-cast-main{
        height: 300px;
        width: 100%;
        transform: scale(0.9);
    }
    .main-info{
        top: 50vh;
        
    }
    
       
    
    
} 

@media  screen and (max-width: 482px) {

    .tagline-overview-container{
        padding-top: 15vh;
        width: 95%;
    }
}

// Small devices (landscape phones, 400px and up)
@media  screen and (max-width: 430px) {

    .main-info{
        top: 60vh;
    }
    .main-title{
        width: 150%;
    }
    .trending-slider{
        transform: scale(1);
        width: 80%;
    }

    .detail-info-container{
        width: 100%;
        overflow: visible;
        transform: scale(0.9);
    }
    .play-trailer-button{
        position: relative;
        top: -120px
    }
    .progress-bar{
        justify-content: center;
        align-items: center;
        padding-top: 2vh;
    }
    
} 


@media  screen and (max-width: 400px) {
    
    .play-trailer-button{
        position: relative;
      top: -120px
    }

    .trending-slider{
        transform: scale(1.3);
        width: 50%;
    }
}